<template>
  <div class="main-body">
    <div class="search-area">
      <div class="search-box">
        <div class="upload-area">
          <el-upload class="avatar-uploader" action="" :before-upload="resetImageUrl" :show-file-list="false">
            <!-- <img v-if="imageUrl" :src="imageUrl" class="avatar" /> -->
            <el-icon class="avatar-uploader-icon">
              <Plus />
            </el-icon>
          </el-upload>
        </div>
        <div class="image-review-area" v-if="imageUrl">
          <div class="image-review">
            <el-image style="width: 140px; height: 70px" :src="imageUrl" fit="contain" />
          </div>
        </div>
        <div class="search-btn-area" v-if="imageUrl">
          <el-button @click="clearSearchImage">图库</el-button>
          <el-button @click="toSearch">搜索</el-button>
        </div>
      </div>
      <div class="add-box">
        <el-upload class="upload-demo" :show-file-list="false" :before-upload="beforeUpload" :on-success="init"
          :action="`${httpRequestUrl}/upload`">
          <el-button type="primary">上传图片</el-button>
        </el-upload>
      </div>
    </div>
    <div class="search-result-area">
      <div class="search-result-box">
        <div class="result-item" v-for="(n, index) in JSON.parse(JSON.stringify(dataList)).splice((page - 1) * pageSize, pageSize)" :key="index">
          <el-image style="width: 140px; height: 140px" :preview-src-list="[n]" :src="n" fit="contain" lazy />
          <div class="delete-btn" v-if="!imageUrl">
            <el-icon class="delete-info" @click="delImage(n)">
              <Delete />
            </el-icon>
          </div>
        </div>
      </div>
    </div>
    <div class="page-area">
      <el-pagination :current-page="page" :page-size="pageSize" :page-sizes="[10, 20, 30, 50]" small background
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
        layout="total, sizes, prev, pager, next, jumper" :total="total" />
    </div>
  </div>
</template>
<script>
import { Plus, Delete } from '@element-plus/icons-vue'
import axios from 'axios'
const httpRequestUrl = 'https://u248120-bf23-b1506178.westc.gpuhub.com:8443'
export default {
  data() {
    return {
      httpRequestUrl,
      dataList: [
      ],
      imageUrl: '',
      imageFile: '',
      pageSize: 50,
      page: 1,
      total: 0
    }
  },
  components: {
    Plus,
    Delete
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      this.imageUrl = null
      this.getDataList()
    },
    getDataList() {
      axios.get(`${httpRequestUrl}/images`).then(res => {
        // console.log(res)
        const { data } = res
        this.dataList = data.images.map(n => {
          return `${httpRequestUrl}/static/${n}`
        })
        this.total = this.dataList.length
        // console.log(Math.ceil(this.dataList.length / this.pageSize))
        // this.total = Number(this.dataList.length ? Math.ceil(this.dataList.length / this.pageSize) : 0)
        console.log(data)
      })
    },
    clearSearchImage() {
      this.imageUrl = null
      this.init()
    },
    handleSizeChange(val) {
      this.pageSize = val
    },
    handleCurrentChange(val) {
      this.page = val
    },
    toSearch() {
      const data = new FormData()
      data.append('file', this.imageFile)
      axios.post(`${httpRequestUrl}/search`, data).then(res => {
        console.log(res)
        const { data } = res
        this.dataList = data.result.map(n => {
          return `${httpRequestUrl}/static/${n}`
        })
      })
    },
    uploadForSearch() {

    },
    uploadToHub() {

    },
    resetImageUrl(file) {
      if (file.size > 1024 * 1024 * 10) {
        this.$message.error('请选择10Mb以内的图片')
        return false
      }
      this.imageFile = file
      // return
      const reader = new FileReader();
      reader.onload = ({ target }) => {
        console.log(target)
        this.imageUrl = target.result
        // console.log(event)
      }
      reader.readAsDataURL(file)
      return false
    },
    beforeUpload(file) {
      if (file.size > 1024 * 1024 * 10) {
        this.$message.error('请上传10Mb以内的图片')
        return false
      }
    },
    delImage(url) {
      axios.delete(`${httpRequestUrl}/delete/${url.split('/')[url.split('/').length - 1]}`).then(() => {
        // console.log(res)
        this.getDataList()
      })
    }
  }
}
</script>
